<template>
    <div id="edit-employee">
        <h3>Edit Employee</h3>
        <div class="row">
            <form @submit.prevent="updateEmployee" class="col s12">
                <div class="row">
                    <input type="text" v-model="employee_id" disabled  required>
                </div>
                <div class="row">
                    <input type="text" v-model="name" required >
                </div>
                <div class="row">
                    <input type="text" v-model="department" required >
                </div>
                <div class="row">
                    <input type="text" v-model="designation" required >
                </div>
                <div class="row">
                    <input type="text" v-model="joining_date" required >
                </div>
                <div class="row">
                    <input type="text" v-model="status" required >
                </div>
                <button type="submit" class="btn">Update</button>
                &nbsp; &nbsp;
                <router-link to="/" class="btn grey">Cancel</router-link>
            </form>
        </div>
    </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name: 'default',
    // applicant_id: 0000,
    data () {
        return {
            employee_id: null,
            name: null,
            department: null,
            designation: null,
            joining_date: null,
            status:null
        }
    },
    beforeRouteEnter(to, from, next) {
        db.collection('applicants').where('employee_id', '==', to.params.employee_id).get()
        .then(querySnapshot => {
            querySnapshot.forEach(doc => {
                next(vm => {
                    vm.employee_id = doc.data().employee_id
                    vm.name = doc.data().name
                    vm.department = doc.data().department
                    vm.designation = doc.data().designation
                    vm.joining_date = doc.data().joining_date
                    vm.status = doc.data().status
                })
            })
        })
    },
    watch: {
        '$route': 'fetchData'
    } ,
    methods: {
        fetchData () {
            db.collection('applicants').where
            ('employee_id', '==', 
            this.$route.params.employee_id).get()
                .then(querySnapshot => {
                    querySnapshot.forEach(doc => {
                        this.employee_id = doc.data().employee_id 
                        this.name = doc.data().name
                        this.department = doc.data().department
                        this.designation = doc.data().designation
                        this.joining_date = doc.data().joining_date
                        this.status = doc.data().status

                    })
                })
        },
        updateEmployee () {
            db.collection('applicants').where
            ('employee_id', '==', 
            this.$route.params.employee_id).get()
                .then(querySnapshot => {
                    querySnapshot.forEach(doc => {
                       doc.ref.update({
                           employee_id: this.employee_id,
                           name: this.name,
                           department: this.department,
                           designation: this.designation,
                           status: this.status
                       })
                       .then(() => {
                           this.$router.push({name: 'view-employee', params: {employee_id: this.employee_id}})
                       })
                    })
                })
        }    
    }
}
</script>
