<template>
    <div id="dashboard">
        <ul class="collection with-header">
            <li class="collection-header">
                <h4>Employees List</h4>
            </li>
            <li v-for="applicant in applicants"
            v-bind:key="applicant.id" 
            class="collection-item">
                <div class="chip"> {{applicant.department}} </div>
                    {{applicant.name}}
                <router-link class="secondary-content"
                v-bind:to="{name: 'view-employee', params: {employee_id: applicant.employee_id}}"> 
                    <i class="fa fa-eye"></i>
                </router-link>
            </li> 
        </ul>
        <div class="fixed-action-btn">
            <router-link to="/new" class="btn-floating btn0large red">
                <i class="fa fa-plus"></i>
            </router-link>    
        </div>
    </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name: 'default',
    data () {
        return {
            applicants: []
        }
    },
    created () {
        db.collection('applicants').orderBy('name').get().then(querySnapshot => {
            querySnapshot.forEach(doc => {
                console.log(doc.id);
                const data = {
                    'id': doc.id,
                    'employee_id': doc.data().employee_id,
                    'name': doc.data().name,
                    'department': doc.data().department,
                    'designation': doc.data().designation,
                    'joining_date': doc.data().joining_date,
                    'status': doc.data().status,
                }
                this.applicants.push(data)
            })
        })
}
}
</script>
