<template>
    <div id="dashboard">
        <ul class="collection with-header">
            <li class="collection-header"> 
                <h4>Employee: {{name}}</h4>
            </li>
            <li class="collection-item">Employee id : {{employee_id}}</li>
            <li class="collection-item">Department: {{department}}</li>
            <li class="collection-item">Designation: {{designation}}</li>
            <li class="collection-item">Joining Date: {{joining_date}}</li>
            <li class="collection-item"><div class="chip"> Status:  {{status}} </div></li>
        </ul>
        <router-link to="/" class="btn grey">Back</router-link>
        &nbsp;
        <button @click="deleteRecord"  class="btn red" ml-2> Delete </button>

        <div class="fixed-action-btn">
            <router-link v-bind:to="{name: 'edit-employee', params: {employee_id: employee_id}}" class="btn-floating btn0large red">
                <i class="fa fa-pencil"></i>
            </router-link>    
        </div>
    </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name: 'view-employee',
    employee_id: '',
    department: '',
    designation: '',
    joining_date: '',
    status: '',
    data () {
        return {
            id: null,
            employee_id: null,
            name: '',
            department: null,
            designation: null,
            joining_date: null,
            status:null
        }
    } ,
    beforeRouteEnter(to, from, next) {
        db.collection('applicants').where('employee_id', '==', to.params.employee_id).get()
        .then(querySnapshot => {
            querySnapshot.forEach(doc => {
                next(vm => {
                    vm.employee_id = doc.data().employee_id
                    vm.name = doc.data().name
                    vm.department = doc.data().department
                    vm.designation = doc.data().designation
                    vm.joining_date = doc.data().joining_date
                    vm.status = doc.data().status
                })
            })
        })
    },
    watch: {
        '$route': 'fetchData'
    } ,
    methods: {
        fetchData () {
            db.collection('applicants').where
            ('employee_id', '==', 
            this.$route.params.employee_id).get()
                .then(querySnapshot => {
                    querySnapshot.forEach(doc => {
                        this.employee_id = doc.data().employee_id 
                        this.name = doc.data().name
                        this.department = doc.data().department
                        this.designation = doc.data().designation
                        this.joining_date = doc.data().joining_date
                        this.status = doc.data().status

                    })
                })
        },
        deleteRecord (){
            if(confirm('Are you sure, you want to delete this record?')) {
                db.collection('applicants').where('employee_id', '==', this.$route.params.employee_id).get()
                .then(querySnapshot => {
                    querySnapshot.forEach(doc => {
                        doc.ref.delete()
                        this.$router.push('/')
                    })
                })
            }
        }
    }
}
</script>