<template>
    <div id="Employee">
        <h3>Employee</h3>
        <div class="row">
            <form @submit.prevent="saveEmployee" class="col s12">
                <div class="row">
                    <input type="text" v-model="employee_id" required >
                    <label>Employee ID#</label>
                </div>
                <div class="row">
                    <input type="text" v-model="name" required >
                    <label>Name</label>
                </div>
                <div class="row">
                    <input type="text" v-model="department" required >
                    <label>Department</label>
                </div>
                <div class="row">
                    <input type="text" v-model="designation" required >
                    <label>Designation</label>
                </div>
                <div class="row">
                    <input type="text" v-model="joining_date" required >
                    <label>Joining Date (MM-DD-YYYY)</label>
                </div>
                <div class="row">
                    <input type="text" v-model="status" required >
                    <label>status</label>
                </div>
                <button type="submit" class="btn">Save</button>
                &nbsp; &nbsp;
                <router-link to="/" class="btn grey">Cancel</router-link>
            </form>
        </div>
    </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name: 'default',
    data () {
        return {
            employee_id: null,
            name: null,
            department: null,
            designation: null,
            joining_date: '07-01-2019',
            status: 'active'
        }
    },
    methods: {
        saveEmployee () {
            db.collection('applicants').add({
                employee_id: this.employee_id,
                name: this.name,
                department: this.department,
                designation: this.designation,
                joining_date: this.joining_date,
                status: this.status
            })
            .then(docRef => this.$router.push('/'))
            .catch(error => console.log(err))
        }
    }
}
</script>
